export default {

	name: "latte-password",

	props: {

		autocomplete: {
			default: "",
			required: false,
			type: String
		},

		id: {
			default: "",
			required: false,
			type: String
		},

		name: {
			default: "",
			required: false,
			type: String
		},

		placeholder: {
			default: "",
			required: false,
			type: String
		},

		show: {
			default: false,
			required: false,
			type: Boolean
		},

		value: {
			default: "",
			required: false,
			type: String
		}

	},

	data()
	{
		return {
			fieldType: this.show ? "text" : "password",
			password: this.value,
			shouldShow: this.show
		};
	},

	computed: {

		bindings()
		{
			const bindings = {};

			if (this.id !== "")
				bindings.id = this.id;

			if (this.name !== "")
				bindings.name = this.name;

			if (this.placeholder !== "")
				bindings.placeholder = this.placeholder;

			return bindings;
		},

		iconClass()
		{
			return this.fieldType === "password" ? "mdi-eye" : "mdi-eye-off";
		}

	},

	methods: {

		toggle()
		{
			this.shouldShow = !this.shouldShow;
		}

	},

	watch: {

		password()
		{
			this.$emit("input", this.password);
		},

		shouldShow()
		{
			this.fieldType = this.shouldShow ? "text" : "password";

			this.$emit("visible", this.shouldShow);
		},

		show()
		{
			this.shouldShow = this.show;
		},

		value()
		{
			this.password = this.value;
		}

	}

}

