export default {

	name: "latte-sorting-button",

	props: {

		ariaLabel: {
			default: null,
			required: false,
			type: String | null
		},

		buttonClass: {
			default: "btn btn-icon btn-text btn-dark",
			required: false,
			type: String
		},

		isSorting: {
			default: false,
			required: false,
			type: Boolean
		},

		isSortingAscending: {
			default: true,
			required: false,
			type: Boolean
		}

	},

	methods: {

		onClick(evt)
		{
			this.$emit("click", evt);
		}

	}

}

