export default {

	name: "latte-tab",

	props: {

		badge: {
			default: "",
			required: false,
			type: String
		},

		icon: {
			default: "",
			required: false,
			type: String
		},

		label: {
			default: "",
			required: false,
			type: String
		}

	},

	data()
	{
		return {
			active: false
		};
	},

	mounted()
	{
		this.$parent.updateChildren();
	},

	watch: {

		active()
		{
			this.$emit("active", this.active);
		}

	}

}

