import { prompt, Buttons } from "../../js/ui/message";
import { translate } from "../../js/i18n";
import { clamp } from "../../js/math";

export default {

	name: "latte-pagination",

	props: {

		controllerBar: {
			default: false,
			required: false,
			type: Boolean
		},

		limit: {
			default: 10,
			required: true,
			type: Number
		},

		navigationControls: {
			default: true,
			required: false,
			type: Boolean
		},

		offset: {
			default: 0,
			required: false,
			type: Number
		},

		sizeEnd: {
			default: 2,
			required: false,
			type: Number,
			validator: num => num >= 0
		},

		sizeMid: {
			default: 1,
			required: false,
			type: Number,
			validator: num => num >= 0
		},

		total: {
			default: 0,
			required: true,
			type: Number
		}

	},

	data()
	{
		return {
			limits: [5, 10, 20, 50, 100]
		};
	},

	computed: {

		currentPage()
		{
			return Math.min(this.totalPages, Math.floor(this.offset / this.limit) + 1);
		},

		totalPages()
		{
			return Math.ceil(this.total / this.limit);
		},

		visiblePages()
		{
			const current = this.currentPage;
			const total = this.totalPages;

			if (this.totalPages === 0)
				return [];

			let dots = false;
			let pages = [];

			if (this.totalPages === (this.sizeEnd + this.sizeMid + 2))
			{
				for (let n = 1; n <= total; n++)
					pages.push(n);
			}
			else
			{
				for (let n = 1; n <= total; n++)
				{
					if (current === n)
					{
						dots = true;
						pages.push(n);
					}
					else if (n <= this.sizeEnd || (n >= current - this.sizeMid && n <= current + this.sizeMid) || n > total - this.sizeEnd)
					{
						dots = true;
						pages.push(n);
					}
					else if (dots)
					{
						dots = false;
						pages.push("...");
					}
				}
			}

			return pages;
		}

	},

	methods: {

		askForPage()
		{
			prompt(translate("latte-ui", "Navigate to page..."), translate("latte-ui", "To which page do you want to go?")).then(r =>
			{
				if (r.button !== Buttons.OK)
					return;

				const page = parseInt(r.input);

				if (isNaN(page))
					return;

				this.navigate(clamp(page, 1, this.totalPages));
			});
		},

		navigate(page)
		{
			this.$emit("navigate", (page - 1) * this.limit);
		}

	}

}

