import Vue from "vue";

export default {

	name: "latte-data-table-actions",

	props: {

		actions: {
			default: () => [],
			required: true,
			type: Array
		},

		row: {
			default: () => ({}),
			required: true,
			type: Object
		}

	},

	methods: {

		createAction(action, row)
		{
			return Vue.extend({

				template: action.template,

				data()
				{
					return {action, row};
				}

			});
		}

	}

}

