export default {

	name: "latte-button-dropdown",

	props: {

		ariaLabel: {
			default: "",
			required: false,
			type: String
		},

		buttonClass: {
			default: "btn-text btn-icon btn-dark",
			required: false,
			type: String
		},

		icon: {
			default: "",
			required: false,
			type: String
		},

		iconAfter: {
			default: "",
			required: false,
			type: String
		},

		iconBefore: {
			default: "",
			required: false,
			type: String
		},

		label: {
			default: "",
			required: false,
			type: String
		},

		marginX: {
			default: -9,
			required: false,
			type: Number
		},

		marginY: {
			default: 0,
			required: false,
			type: Number
		},

		small: {
			default: false,
			required: false,
			type: Boolean
		},

		tooltip: {
			default: "",
			required: false,
			type: String
		}

	},

	data()
	{
		return {
			isOpen: false,
			isRipple: false
		};
	},

	created()
	{
		this.isRipple = this.$parent.$options.name === "latte-ripple";
	},

	computed: {

		avatarUrl()
		{
			if (this.icon.substr(0, 6) === "avatar")
				return this.icon.substr(7);

			return null;
		},

		buttonClasses()
		{
			const classes = this.buttonClass.split(" ");

			if (this.small)
				classes.push("btn-sm");

			if (this.isOpen)
				classes.push("is-open", "tooltip-disabled");

			if (this.isRipple)
				classes.push("is-ripple");

			return classes;
		},

		iconClasses()
		{
			return ["mdi", `mdi-${this.icon}`];
		},

		iconAfterClasses()
		{
			return ["mdi", `mdi-${this.iconAfter}`];
		},

		iconBeforeClasses()
		{
			return ["mdi", `mdi-${this.iconBefore}`];
		},

		self()
		{
			return this;
		}

	},

	methods: {

		onClose()
		{
			this.isOpen = false;
		},

		onOpen()
		{
			this.isOpen = true;
		}

	}

}

