export default {

	name: "latte-datepicker",

	props: {

		id: {
			default: "date",
			required: false,
			type: String
		},

		name: {
			default: "date",
			required: false,
			type: String
		},

		placeholder: {
			default: "",
			required: false,
			type: String
		},

		value: {
			default: () => new Date(),
			required: false,
			type: Date
		}

	},

	data()
	{
		return {
			current: this.value
		};
	},

	computed: {

		inputValue()
		{
			return this.moment(this.current).format("YYYY-MM-DD");
		}

	},

	watch: {

		current()
		{
			this.$emit("input", this.current);
			this.$refs.popup.close();
		},

		value()
		{
			this.current = this.value;
		}

	}

}

